.form__field {
  margin-bottom: 21px;
}

.form__field input {
  width: 327px;
  margin: 0;
  padding: 0;
  padding-top: 13px;
  padding-bottom: 8px;
  padding-left: 30px;
  padding-right: 30px;

  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  color: #1C1C1C;

  border: 1px solid #C9CCD4;
  border-radius: 4px;
}

.form__field input:hover {
  opacity: 0.8;
}

.form__field input:active {
  opacity: 0.5;
}
