@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Overpass";
  src: url("../fonts/overpass-semibold.woff2") format("woff2"), url("../fonts/overpass-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal; }

.page-header {
  background-color: #171717;
  background-image: url("../img/bg-bike-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-bike-desktop@2x.jpg"); } }

@media screen and (max-width: 768px) {
  .page-header {
    background-image: none; } }
  @media screen and (max-width: 768px) and (min-resolution: 144dpi), screen and (max-width: 768px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: none; } }

